.social {
  width: 114px;
  padding: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(3, 20px);
  justify-content: space-between;

  @include vp-1023 {
    width: 13%;
  }

  @include vp-767 {
    width: 92px;
  }
}

.social__item {
  width: 20px;
  height: 20px;
  margin-bottom: 10px;
  display: flex;

  @include vp-1023 {
    width: 16px;
    height: 16px;
  }

  @include focus {
    outline: 2px solid #0092bb;
  }
}

.social__item a {
  cursor: pointer;

  @include focus {
    outline: 2px solid #0092bb;
  }
}

.social__item-icon {
  fill: $second-color;

  @include vp-1023 {
    width: 16px;
    height: 16px;
  }

  @include hover {
    @media (hover: hover) {
      fill: #00c8ff;
    }
  }
}
